import React from 'react';
import useMailchimp from './Hooks'
import Status from './Hooks'
import jsonp from 'jsonp'

import Loading from './Loading';
import Error from './Error';
import Success from './Success';

export function useScreenWidth(thingy,thingy2) {
  // const SubscribeForm = () => {
    // console.log(thingy);
  const [form, setForm] = React.useState({firstName: '', email: ''})
  const [status2, setStatus2] = React.useState(null);
  var {subscribe, status, error, value} = useMailchimp('https://gmail.us7.list-manage.com/subscribe/post?u=7247676ecb0306e26c5320a78&amp;id=2a8a1ad9a4')

  const handleInputChange = event => {
    const target = event.target
    const name = target.name
    const value = target.value
    setForm(form => ({
      ...form,
      [name]: value,
    }))
  }
  const sendThisToParent = () =>{
    // console.log("hey", thingy);
    thingy();
    thingy2();
    setStatus2("IDLE");
  }


  const statusChange = () =>{
    // status = "IDLE"
    thingy2();
    setStatus2("IDLE");
  }

  const handleSubmit = () => {
    // We are performing some simple validation here.
    // I highly recommend checking a fully-fledged solution
    // for forms like https://react-hook-form.com/
    thingy2();
    setStatus2(null);
    if (form.email === '') {
    	// console.log("empty");
    	return}

    subscribe({
      FNAME: "noname",
      EMAIL: form.email,
    })
  }

  const afterSubmission = event => {
    event.preventDefault();
	}

// if (status === "LOADING") {
//     return <Loading />
//   }
    if (status2 === "IDLE") {
    
  }else if(status === "LOADING") {
    return <Loading />
  }
  else if (status === "ERROR") {
  	if (error != undefined) {
  		if (error.includes('An email address must contain')) {
  		return <Error name="noat" onClickk={statusChange} />

	  }else if (error.includes('The domain portion of the email address is invalid')) {
  		return <Error name="domain" onClickk={statusChange} />
	    
	  }else if (error.includes('already subscribed')) {
  	  	return <Error name="already" onClickk={statusChange} />
	  }else{
	  	return <Error name="default" onClickk={statusChange} />
	  }
  	}else{
  		
  	}
  }else if (status === "SUCCESS") {
  	return <Success onClickk={sendThisToParent} />
  }


  return (
  	<div>
    <form className="form" style={{display:"inherit"}} onSubmit = {afterSubmission} autocomplete="off" >
      <input
        id="firstName"
        name="firstName"
        placeholder="name"
        onChange={handleInputChange}
        className="input"
        style={{display:"none"}}
      />
      <br />
      <input className="input" id="email" name="email" placeholder="email" onChange={handleInputChange} />
      <br /> 
      <button className="inputButton" onClick={handleSubmit}>Register </button>
    </form>
    </div>
  )
// }

}

